import {ChatContext} from './ChatContext'
import React, { useContext } from 'react'
import Message from './Message'
import ReactDOMServer from 'react-dom/server'
import socketIOClient from 'socket.io-client'
import './Chat.css'

const socket = socketIOClient.io('https://drmatthapi.herokuapp.com/')

socket.on('new_message', (data) => {
  add(data.username, data.message, data.color)
})

function add(username, message, color) {
  const newMessage = ReactDOMServer
    .renderToStaticMarkup(<Message username={username} message={message} color={color}/>)
  document.getElementById('messages').innerHTML += newMessage
}

function Chat() {
  const context = useContext(ChatContext)
  
  function handleSubmit(e) {
    e.preventDefault()
    if (context.state.name === "") alert('primero elije un nombre de usario')
    else {
      const mensaje = document.getElementById('mensaje')
      socket.emit('new_message', {
        username: context.state.name,
        message: mensaje.value,
        color: context.state.color
      })
      mensaje.value = ''
    }
  }
  return (
    <div className="container">
      <div className="chat-box">
        <div id="messages">
          {/** aqui se van a juntar los mensajes y creo este por defecto para evitar re-renders */}
          <div>
            <span style={{ color: '#e33b3b' }}>__bot__: </span>
            <span style={{ color: 'white' }}>bienvenido al chat!</span>
          </div>
        </div>
      </div>
      <form className="form" onSubmit={(e) => handleSubmit(e)} autoComplete="off">
        <input className="input" type="text" id="mensaje" placeholder="Escribe un mensaje aquÃ­"></input>
        <button className="button" id="submitButton">Enviar Mensaje</button>
      </form>
    </div>
  )
}
export default Chat