import React from 'react'

function Footer() {

	return (
		<footer style={styles.footer}>
			<p style={styles.text}>
				&copy; 2020 - {new Date().getFullYear()} GatoCorp, Inc.
			</p>
		</footer>
	)
}

const styles = {
	footer: {
		"background": "#708bb8",
		"display": "flex",
		"flexDirection": "column",
		"marginTop": "20px",
		"padding": "10px 0px"
	},
	text: {
		textAlign: 'center', 
		margin: 0, 
	}
}

export default Footer