import { useState, useContext } from 'react'
import {ChatContext} from './ChatContext'

function getColor() {
  const colors = {
    red: 'e33b3b',
    yellow: 'f5e15f',
    green: '3edb3b',
    blue: '43dedb',
    purple: 'b63be3',
    lime: 'c2f745'
  }
	let randomNumber = Math.floor(Math.random() * Object.keys(colors).length)
	return colors[Object.keys(colors)[randomNumber]]
}

function getUrlAvatar(name, background, color) {
  return `https://ui-avatars.com/api/?name=${name}&background=${background}&color=${color}`
}

function User() {
  const context = useContext(ChatContext)

  const [urlAvatar, setUrlAvatar] = useState("")

  function handleSubmit(e) {
    e.preventDefault()
    let color = getColor() // esto hay que cambiar, nose como
    context.setColor(color)
    setUrlAvatar(getUrlAvatar(context.state.name, color, '000'))
  }

  return (
    <div style={styles.container}>
      <form autoComplete="off">
        <input style={styles.input} type="text" placeholder="nombre de usuario"
          onChange={(e) => context.setName(e.target.value)}
        />
        <button style={styles.button} onClick={(e) => handleSubmit(e)}>Cambiar</button>
        <img src={urlAvatar} alt="profile"></img>
      </form>
    </div>
  )
}

const styles = {
  container: {
    "width": "80vw",
    "height": "10vh",
    "margin": "auto",
  },
  input: {
    "fontSize": "15px"
  },
  button: {
    "fontSize": "15px"
  }
}

export default User